.card {
  background: #fff;
  opacity: .95;
  border-radius: 1px;
  height: 150px;
  margin: 4px;
  position: relative;
  width: 250px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:rgb(255, 255, 255);
}
.card > .img-container > img {
  width: 60%
}



.wrapper {
  padding-top: 50px;
  background: url(/clicky-game/static/media/mklogo.e0816bc3.png) no-repeat center center fixed; ;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.title {
  width: 100%;
  text-align: center;
  font-size: 24px;
  text-decoration: none;
  color: rgb(165, 0, 27);
  text-shadow: (2px 2px 4px #ffffff);
  margin-bottom: 50px;
}

nav.navbar {
    width: 100%;
    text-align: center;
    font-size: 18px;
    text-decoration: none;
    color: rgb(255, 255, 255);
    margin-bottom: 0;
    background: url(/clicky-game/static/media/mktitle.0dfac580.png) no-repeat left; 
  }

  nav.p {
    color: white;
  }
.title {
  width: 100%;
  text-align: center;
  margin-bottom: 10px;
}


html{
  background: url(/clicky-game/static/media/mklogo.e0816bc3.png) no-repeat center center fixed;
  background-size: cover;
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

